"use client";

import { useState, useEffect } from "react";
import { Key, CheckCircle2 } from "lucide-react";
import { isAPIKeyConnected } from "@/lib/api-key";
import { APIKeyModal } from "./api-key-modal";

export function APIKeyIndicator() {
  const [connected, setConnected] = useState(false);
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    setConnected(isAPIKeyConnected());
    
    // Listen for storage changes
    const handleStorageChange = () => {
      setConnected(isAPIKeyConnected());
    };
    
    window.addEventListener("storage", handleStorageChange);
    // Also check periodically (in case of same-tab updates)
    const interval = setInterval(() => {
      setConnected(isAPIKeyConnected());
    }, 1000);

    return () => {
      window.removeEventListener("storage", handleStorageChange);
      clearInterval(interval);
    };
  }, []);

  if (connected) {
    return (
      <div className="flex items-center gap-2 px-3 py-1.5 rounded-lg glass border border-green-500/30 bg-green-500/10">
        <CheckCircle2 className="w-4 h-4 text-green-400" />
        <span className="text-xs font-medium text-green-400 hidden sm:inline">API Connected</span>
      </div>
    );
  }

  return (
    <>
      <button
        onClick={() => setShowModal(true)}
        className="flex items-center gap-2 px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors"
      >
        <Key className="w-4 h-4 text-muted-foreground" />
        <span className="text-xs font-medium text-muted-foreground hidden sm:inline">
          Connect API Key
        </span>
      </button>
      <APIKeyModal
        isOpen={showModal}
        onClose={() => setShowModal(false)}
        onSave={() => {
          setShowModal(false);
          setConnected(true);
        }}
      />
    </>
  );
}
